const { handleFailCb } = require("../utils");

function checkSet(agrs){
    const {
        storager, 
        storageList,
        key,
        failCb
    } = agrs;

    let errorMsg = "";
    
    // 
    switch(true){
        case !storageList.hasOwnProperty(key):
            errorMsg = `src/constants${key}`;
            break;
        case storager.hasOwnProperty(key):
            errorMsg = `${key}key`;
            break;
    }

    handleFailCb({errorMsg, failCb})
}

module.exports = checkSet;